@import url(https://fonts.googleapis.com/css?family=Marmelad);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
#root {
  height: 100%;
}

.App {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
	-webkit-align-items: center;
	        align-items: center;
	-webkit-flex-direction: column;
	        flex-direction: column;
}
body {
  background: -webkit-linear-gradient(bottom left, #71AE51 1%, #F3BA4B 25%, #E6873B 40%, #CF4845 60%, #8B4593 80%, #449CD7 100%);
  background: linear-gradient(to top right, #71AE51 1%, #F3BA4B 25%, #E6873B 40%, #CF4845 60%, #8B4593 80%, #449CD7 100%);
}



